.card {
   background: #fff;
   border-radius: 2px;
   height: 200px;
   margin: 1rem;
   position: relative;
   width: 200px;
   -webkit-box-shadow: 0 3px 6px #999, 0 3px 6px #999;
           box-shadow: 0 3px 6px #999, 0 3px 6px #999;
   text-align: left;
 }
 
 .card > .img-container {
   height: 100%;
   width: 100%;
   overflow: hidden;
   text-align: center;
   /* background:#6CADDC; */
 }
 .card > .img-container > img {
   width: 200px;
   height: 200px;
 }


